import React from "react"
import Slider from "react-slick"
import home_img_store from "./HomeSliderImages"

function HomeSlider ({fI,tI, sI}){
    var settings = {
        slidesToShow: 1,
        dots: false,
        arrows: false,
        infinite: true,
        fade: true,
        autoplay: true,
        autoplaySpeed: 5000,
        cssEase: 'linear'
    }
    
    return (
        <Slider {...settings}>
            {home_img_store.data.map((item) =>
                <div className="home-slider-image" style="z-index:1">
                    <img src={item.val} />
                </div>
            )}
        </Slider>
    )
}

export default HomeSlider