import React from "react";
import portfolio_img_store from "./PortfolioImages";
import { Fancybox } from "@fancyapps/ui/dist/fancybox/";
import "@fancyapps/ui/dist/fancybox/fancybox.css";
import useFancybox from "./useFancybox";


function createGal(){

            for (let i=0;i<portfolio_img_store.data2.length;i++){
                
                let newdiv = document.getElementById(String("gallery" + i))
            for (let x=0;x<portfolio_img_store.data2[i].length;x++){
                let newimg = document.createElement("img")
                let newa = document.createElement("a")
                newa.setAttribute("class","portfolio-gallery-item wow fadeInLeft")
                newa.setAttribute("data-fancybox","portfolio-gallery"+i)
                newa.setAttribute("id",i)
                newa.setAttribute("href", portfolio_img_store.data2[i][x])
                newimg.setAttribute("src",portfolio_img_store.data2[i][x])
                newimg.setAttribute("class","portfolio-item-img")
                newimg.setAttribute("loading","lazy")
                newa.appendChild(newimg)
                newdiv.appendChild(newa)
                
            }}
}

export default createGal