import React from "react";
import './App.css';
import portfolio_img_store from "./PortfolioImages";
import { Fancybox } from "@fancyapps/ui/dist/fancybox/";
import "@fancyapps/ui/dist/fancybox/fancybox.css";
import useFancybox from "./useFancybox";
import Slider from "react-slick";
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import createGal from "./createGal";
import Scroll from "./Scroll";
import clearGal from "./clearGal";
import WOW from "wow.js"


const Portfolio = () => {
    new WOW().init();
    const [fancyboxPortfolioGal] = useFancybox({
            Carousel: {
                Thumbs: {
                type: "classic",
            }},
            on: {
    close: () => {
            clearGal()
            createGal()
      

    }}
        });

    
    var settings = {
        slidesToShow: 1,
        dots: false,
        arrows: true,
        infinite: true,
        fade: true,
        autoplay: false,
        cssEase: 'linear',
        
    }
    
    window.onload = () => {
        createGal()
    }
    return(
        
    <div ref={fancyboxPortfolioGal}>
        <h1 className="portfolio-heading inter-700 wow fadeInRight">Портфолио</h1>

        <div id="SliderGal">
        <Slider {...settings}>
        
        {
        portfolio_img_store.data2.map((item, index) =>
            
                <div key={index}>
                    <h2 className="portfolio-gallery-heading inter-400 wow fadeInLeft">{item.name}</h2>
                    
                    <div id={"gallery"+index} className="portfolio-gallery wow fadeInLeft">
                    
                    </div>
                </div>
        )
        }
        </Slider>
        </div>
    </div>
    
    )


}
export default Portfolio