import React from "react";

function Get(yourUrl){
    var Req = new XMLHttpRequest(); // a new request
    Req.open("GET",yourUrl,false);
    Req.send(null);
    return Req.responseText;          
}
let res = JSON.parse(Get("https://script.google.com/macros/s/AKfycbwPs9ljQdQzNFTxy_3duIwLBonLHbt26NSS6AUgmMGZPm801UbHZzbmgjtLWVIu4MNmSg/exec"))

